
import Vue from 'vue';
import Router from 'vue-router';
import RegisterForm from './components/RegisterForm.vue';
import Users from './components/Users.vue';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'main',
            component: RegisterForm
        },
        {
            path: '/users',
            name: 'users',
            component: Users
        },
    ],
    mode: 'history'
});